.menu__bar {
    display: flex;
    justify-content: space-between;
}

.menu__title {
    font-family: Playfair Display;
    color: inherit;
    font-weight: normal;
    font-size: 1.5rem;
    display: inline-block;
}
/* .menu__title::before {
    content: url(data:image/svg+xml; utf8; ../../assets/img/logo.svg);
    display: inline-flex;
    content: "";
    background-image: url(../../assets/img/logo.svg);
    background-size: 16px 16px;
    width: 16px;
    height: 16px;
} */
.menu__logo {
    /* content: url(data:image/svg+xml; utf8; ../../assets/img/logo.svg); */
    display: inline-flex;
}

.menu__icon {
    color: inherit;
    font-size: 1rem;
    margin: 0 1em;
    cursor: pointer;

}

.menu__hamburguer {
    display: none;
}

.menu__navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    height: var(--height-nav);
}
.menu__item {
    list-style: none;
    position: relative;
    margin: 0 1.5em;
    height: 100%;
}

.menu__link {
    text-decoration: none;
    color: inherit;
    line-height: var(--height-nav);
}


.menu__link--active::after {
    content: "";
    position: absolute;
    width: 2px;
    height: 1em;
    background: var(--black);
    bottom: 0;
    left: 45%;
    box-shadow: -0.4px 0px 0px rgba(0, 0, 0, 0.6);
    -webkit-animation: animationLineNav .5s;
            animation: animationLineNav .5s;
}

.menu__link--footer {
    text-decoration: line-through;
}

.menu__icon:active {
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.2);
}

@-webkit-keyframes animationLineNav {
    0% {
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes animationLineNav {
    0% {
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@media screen and (max-width: 500px) {
    .nav {
        display: none;
    }
    .menu__social {
        display: none;
    }
    .menu__searchAndCart {
        display: none;
    }
    .menu__bar {
        padding: 15px 0;
    }
    .menu__hamburguer {
        display: flex;
        font-size: 1.5rem;
    }
}
.footer {
    background: #fff;
}

.footer__container {
    display: flex;
    justify-content: space-between;
    padding: 40px 100px;
    border-top: 1px solid #EFEFF4;
}

.footer__container > div{
    max-width: 25%;
    display: flex;
    flex-direction: column;
    min-width: 150px;
    margin-top: 10px;
}
.footer_list {
    list-style: none;
    padding: 0;
    font-size: 1rem;
    color: var(--gray6);
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    grid-gap: 10px;
    gap: 10px;
}

.footer__description div {
    display: flex;
    align-items: center;
}
.footer__description .paragraph {
    color: var(--gray6);
    font-size: 1rem;
    margin-top: 20px;
    margin-left: 8px;
}
.footer__title {
    font-size: 1.5rem;
    font-family: Playfair Display;
    font-weight: normal;
}

.footer__link {
    text-decoration: none;
    color: inherit;
}






.copy {
    background: var(--gray2);
    height: 70px;
    
    padding: 40px 100px;
}

.copy__container {
    display: flex;
    justify-content: space-between;
    height: 100%;
    align-items: center;
}
.copy__text {
    color: #8E8E93;
    font-size: 1rem;
    /* padding-left: 100px; */
}

.copy__icons {
    width: 200px;
}


@media screen and (max-width:800px) {
    .footer__container {
        padding: 40px 10px;
    }
}
@media screen and (max-width: 500px) {
    .footer__container {
        padding: 10px 5px 40px;
        flex-wrap: wrap;
    }
    .copy {
        padding: 40px 10px;
    }
}
.socialIcons {
    display: flex;
    justify-content: space-around;
    color: var(--black);
}
